.card-base,.card-gradient,.card-glow,.card-floating,.card-heavy,.card-light{background:var(--card-bg-medium);backdrop-filter:var(--blur-medium);border:var(--border-soft);border-radius:var(--radius-medium);box-shadow:var(--shadow-medium);transition:var(--transition-smooth);position:relative;overflow:hidden}.card-base::before,.card-gradient::before,.card-glow::before,.card-floating::before,.card-heavy::before,.card-light::before{content:"";position:absolute;top:0;left:0;right:0;height:1px;background:linear-gradient(90deg, transparent 0%, rgba(255, 255, 255, 0.8) 50%, transparent 100%);opacity:.6}.card-base:hover,.card-gradient:hover,.card-glow:hover,.card-floating:hover,.card-heavy:hover,.card-light:hover{transform:translateY(-2px);box-shadow:var(--shadow-floating);background:var(--card-bg-heavy);border-color:hsla(0,0%,100%,.6)}.card-light{background:var(--card-bg-light);backdrop-filter:var(--blur-light);box-shadow:var(--shadow-soft)}.card-heavy{background:var(--card-bg-heavy);backdrop-filter:var(--blur-heavy);box-shadow:var(--shadow-strong)}.card-floating{transform:translateY(-4px);box-shadow:var(--shadow-floating)}.card-floating:hover{transform:translateY(-8px);box-shadow:0 16px 60px rgba(0,0,0,.18)}.card-glow::after{content:"";position:absolute;top:-2px;left:-2px;right:-2px;bottom:-2px;background:linear-gradient(45deg, rgba(102, 204, 153, 0.1), rgba(75, 184, 164, 0.1), rgba(102, 204, 153, 0.1));border-radius:inherit;z-index:-1;opacity:0;transition:var(--transition-smooth)}.card-glow:hover::after{opacity:1}.card-gradient{background:linear-gradient(135deg, rgba(255, 255, 255, 0.9) 0%, rgba(248, 250, 251, 0.8) 50%, rgba(255, 255, 255, 0.85) 100%)}.card-content{padding:1.5rem;position:relative;z-index:1}.card-header{padding:1.5rem 1.5rem 1rem 1.5rem;border-bottom:1px solid hsla(0,0%,100%,.2);position:relative;z-index:1}.card-footer{padding:1rem 1.5rem 1.5rem 1.5rem;border-top:1px solid hsla(0,0%,100%,.2);position:relative;z-index:1}@media(max-width: 768px){.card-conten